.wrapper {
    display: flex;
    justify-content: center;
    margin-top : 22.5vh;
    .Nav {
        width : 35vw;
        transform: translateX(-5vw);
        text-decoration: none;
        .link {
            text-decoration: none;
            animation: anim 0.6s;
            .el {
                width : 35vw;
                height : 55vh;
                padding : 0;
                background-color: transparent;
                border-radius: 20px;
                border: solid 0.5vh #47bd56;
                box-sizing: border-box;
                transition: 0.7s;
                animation: anim 0.6s;

                .image1 {
                    width : 20vw;
                    height : 40vh;
                    margin-left: 7.5vw;
                    background-image: url('./pixil-frame-06.png');
                    background-size: cover;
                    background-repeat: no-repeat;
                    transform: translateY(3vh);
                }
                .title {
                    text-align: center;
                    text-decoration: none;
                    color :#47bd56;
                    transition: 0.7s;
                    font-size : 2rem;
                }
            }   

            .el:hover {
                transform: scale(1.05);
                background-color: #47bd56;
                .title {
                    color : white;
                }
            }
        }
    }
    .Nav2 {
        @extend .Nav;
        transform: translateX(5vw);
        .el {
            .image2 {
                width : 20vw;
                height : 40vh;
                margin-left: 7.5vw;
                background-image: url('./pixil-frame-05.png');
                background-size: cover;
                background-repeat: no-repeat;
            }
        }
    }
}


@media screen and (min-width : 1440px) {
    .wrapper {
        .Nav {
            width : 40vw;
            .link {
                .el {
                    width : 40vw;
                    .image1 {
                        width : 25vw;
                    }
                    .title {
                        font-size : 3rem;
                    }
                }
            }
        }
        .Nav2 {
            .link {
                .el {
                    width : 40vw;
                    .image2 {
                        width : 25vw;
                    }
                    .title {
                        font-size : 3rem;
                    }
                }
            }
        }
    }
}
@media screen and (min-width : 2500px) {
    .wrapper {
        .Nav {
            width : 40vw;
            .link {
                .el {
                    width : 40vw;
                    .image1 {
                        width : 20vw;
                        margin-left: 10vw;
                    }
                    .title {
                        font-size : 5rem;
                    }
                }
            }
        }
        .Nav2 {
            .link {
                .el {
                    width : 40vw;
                    .image2 {
                        width : 20vw;
                        margin-left: 10vw;
                    }
                    .title {
                        font-size : 5rem;
                    }
                }
            }
        }
    }
}
@keyframes anim {
    from {
        opacity : 0;
    }
    to {
        opacity: 1;
    }

}
